@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

@tailwind base;
@tailwind components;
@tailwind utilities;
.tab-link {
    padding: 10px 20px;
    text-decoration: none;
    position: relative;
  }
  .tab-link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    transition: transform 0.3s ease;
    color:#00b2df;
  }
  
  .tab-link:hover::after,
  .tab-link:focus::after {
    color: #00b2df !important;
    background-color: #00b2df;
    transform: scaleX(1);
  }
  

  .tab-link.active::after {
    color: #00b2df;
    background-color: #00b2df;
    transform: scaleX(1);
  }
  .active{
    color: #00b2df;
  }